body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Made with love by Chyper*/
@font-face{
    font-family:'ms95'; 
    src: url(/win95-player-reactjs/static/media/ms-sans-serif.9686cb7d.ttf);
  }
 
 
 *{font-family:"ms95"}
 


body {
    font-family:"ms95";
    font-size: 13px;
    background-color: #008080;
    text-decoration: none;
    -webkit-transform: scale(1.9);
            transform: scale(1.9);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    overflow: hidden;
   
}
.windows {    
    border: 1px solid;
    border-left-color: #ffffff;
    border-top-color: #ffffff;   
    background-color: #c3c7cb;
    border-right-color: #868a8e;
    
}
.windows-box {    
    border: 1px solid;    
    border-top-color: #c3c7cb;   
    border-left-color: #c3c7cb;  
    background-color: #c0c0c0;
    border-bottom-color: #444;
    border-right-color: #000000;
    width: 410px;
    height: 280px;
   
}

.windows-header {
    margin: 1px 1px 1px 1px; 
    padding: 1px 1px 1px 1px;     
    background-image: linear-gradient(to right, #0000aa, #0082aa);; 
    height: 25px;      
}

.windows-title {  
    padding-top: 1px;   
    color: white;   
    position: relative; 
}

.windows-button {
    border: 2px solid;   
    border-bottom-color: #444;
    border-right-color: #444;
    border-left-color: #a6a6a6;
    border-top-color: #a6a6a6;
    background-color:  rgb(192,192,192);    
}

button:active{  
    background-color:  rgb(192,192,192);  
    border: 0.9px solid;   
    border-bottom-color: rgb(0, 0, 0);
    border-right-color: rgb(0, 0, 0);
    border-left-color: #000000;
    border-top-color: #000000;
}

.cd-player {
    background-image: url(/win95-player-reactjs/static/media/cd-player.c5a5dc20.png);
    background-repeat: no-repeat;
    padding-left: 20px;
    padding-bottom: 2px    
}

.windows-toolbar {
    list-style:none;
    display: inline;
    padding: 2px;
    margin-left: -10px;
}

.toolbar-item {
    margin-left: 15px;
    display: inline-block;
    list-style:none;
    padding: 1px;
    text-decoration: none;
}

.toolbar-item:first-letter { 
    text-decoration: underline;
    cursor: pointer;
}

.windows-link {
     color: inherit; 
} 

.clock-display {  
    margin-top: 5px;  
    border: 1px solid;   
    border-bottom-color:  #444 ;
    outline: 1px white;
    border-right-color: #444;
    border-left-color: #a6a6a6;
    border-top-color: #a6a6a6;    
    background-color:  black; 
    width: 240px;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.text-clock-display {
    text-align: center;
    font-size: 30px;    
    color: #aaaa55;
}

.player-control {
    padding-left: 45px;  
    padding-top: 5px;
}

.play-button {    
    border: 1px solid;    
    border-top-color: #ffffff;   
    border-left-color: #dbdada;;  
    background-color: #c0c0c0;
    border-bottom-color: #000000;
    border-right-color: #000000;
    width: 45%;
    height: 11%;   
    font-family:"ms95"; 
}

.player-button {
    margin: 1px;
    margin-bottom: 5px;
    border: 1px solid;    
    border-top-color: #ffffff;   
    border-left-color: #dbdada;;  
    background-color: #c0c0c0;
    border-bottom-color: #000000;
    border-right-color: #000000;
    width: 21%;
    height: 11%;
    font-family:"ms95";
}




.player-input {
    padding-left: 5px;
    width: 100%;
    height: 7%; 
    font-family:"ms95";
    font-size: 13px;
    border: 1px solid;    
    border-top-color: #000000;   
    border-left-color: #000000;;  
    background-color: #ffffff;
    border-bottom-color: #c3c7cb;
    border-right-color: #c3c7cb;
}
.player-input-of {
    padding-left: 5px;
    width: 100%;
    height: 7%; 
    font-family:"ms95";
    font-size: 13px;
    border: 1px solid;    
    border-top-color: #000000;   
    border-left-color: #000000;  
    background-color:  #c3c7cb;
    border-bottom-color: #c3c7cb;
    border-right-color: #c3c7cb;
}


.player-input-status {
    padding-left: 5px;
    width: 50%;
    height: 7%; 
    font-family:"ms95";
    font-size: 13px;
    border: 1px solid;    
    border-top-color: #000000;   
    border-left-color: #000000;  
    background-color:  #c3c7cb;
    border-bottom-color: #c3c7cb;
    border-right-color: #c3c7cb;
}


.overlay-win {
    position: fixed; /* Sit on top of the page content */
    display: none; /* Hidden by default */
    width: 100%; /* Full width (cover the whole page) */
    height: 100%; /* Full height (cover the whole page) */
    top: 0; 
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5); /* Black background with opacity */
    z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
    cursor: pointer; /* Add a pointer on hover */
}
